<template>
  <div class="field is-horizontal">
    <div class="field-label is-normal">
      <label class="label">Skills</label>
    </div>
    <div class="field-body">
      <div class="field" v-for="skill in skills" :key="skill.id">
        <div class="control">
          <div class="checkbox">
            <label class="checkbox">
              <input type="checkbox" />
              <skill-tag :skill="skill"></skill-tag>
            </label>
          </div>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
import SkillTag from '@/components/item_card/SkillTag'
export default {
  name: 'skill-filter',
  components: {
    SkillTag
  },
  computed: {
    skills () {
      return this.$store.state.skills
    }
  }

}
</script>

<style>

</style>



