<template>
  <div class="field is-horizontal">
    <div class="field-label">
      <label class="label">Seasons</label>
    </div>
    <div class="field-body">
      <div class="field" v-for="season in seasons" :key="season.id">
        <div class="control">
          <div class="checkbox">
            <label class="checkbox">
              <input type="checkbox" />
              <season-tag :season="season"></season-tag>
            </label>
          </div>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
import SeasonTag from '@/components/item_card/SeasonTag'
export default {
  name: 'season-filter',
  components: {
    SeasonTag
  },
  computed: {
    seasons () {
      return this.$store.state.seasons
    }
  }

}
</script>

<style>

</style>



